// Component  - TransactionHistoryFactory
// DESCRIPTION - This component is showing history of transaction done for property module.

import React, { useState, useEffect } from 'react'
import axios from 'axios';
import ListTable from '../../Components/Common/ListTable/ListTable'
import CustomErrorBoundary from '../../Components/Common/CustomErrorBoundary'
import ApiHeader from '../../Components/ApiList/ApiHeader';
import BrandLoader from '../../Components/Common/BrandLoader';
import ServerErrorCard from '../AdvertisementModule/ServerErrorCard';
import CommonModal from '../../Components/GlobalData/CommonModal';


function TransactionHistoryFactory(props) {

    const [readyMadeListData, setreadyMadeListData] = useState();
    const [readyMadeListStatus, setreadyMadeListStatus] = useState(false);
    const [erroState, seterroState] = useState(false);
    const [isLoading, setisLoading] = useState(false);

    // GETTING TABLE COLUMN FROM PARENT COMPONENT
    let columnSchema = [...props?.transactionRules?.tableColumns]

    useEffect(() => {
        fetchTranscationList();
    }, [])

    const fetchTranscationList = () => {
        setisLoading(true)
        seterroState(false)
        setreadyMadeListStatus(false)

        axios[props?.transactionRules?.api?.api_getTranscationHistory?.method](props?.transactionRules?.api?.api_getTranscationHistory?.url, ApiHeader())
            .then(function (response) {
                if (response?.data?.status) {
                    setreadyMadeListData(response?.data?.data)
                    setreadyMadeListStatus(true)
                } else {
                    seterroState(true)
                }
                setisLoading(false)
            })
            .catch(function (error) {
                console.log('Server is too busy to respond, Please try again later', error);
                seterroState(true)
                setreadyMadeListStatus(false)
                setisLoading(false)
            })

    }
    if (erroState) {
        return (
            <CommonModal>
                <ServerErrorCard title="Server is busy" desc="Server is too busy to respond. Please try again later." buttonText="View Dashboard" buttonUrl="/propertyDashboard" />
            </CommonModal>
        )
    }

    if (!erroState) {
        return (
            <>
                <div className='w-ful md:px-10 md:pt-5 mx-auto'>
                    {isLoading && <BrandLoader />}
                    {readyMadeListStatus && (
                        <CustomErrorBoundary errorMsg="Some bug found" >
                            <ListTable
                                assessmentType={false}
                                columns={columnSchema}
                                dataList={readyMadeListData}
                                export={false}
                            />
                        </CustomErrorBoundary>
                    )}
                </div>
            </>
        )
    }
}

export default TransactionHistoryFactory