// Component  - CitizenAllTransactions
// DESCRIPTION - This component is the factory for all transaction related views so that we can make a list component view just passing the api and 
// column name

import React from 'react'
import { useNavigate } from 'react-router-dom'
import CitizenApplyApiList from '../../../Components/CitizenApplyApiList'
import CustomErrorBoundary from '../../../Components/Common/CustomErrorBoundary'
import { nullToNA } from '../../../Components/Common/PowerUps/PowerupFunctions'
import useSetTitle from '../../../Components/GlobalData/UseSetTitle'
import TitleBar from '../../Property/TitleBar'
import TransactionHistoryFactory from '../TransactionHistoryFactory'

function CitizenAllTransactions() {

  useSetTitle('Property Payment History')   // SETTING THE TITLE OF COMPONENT
  const navigate = useNavigate()
  const { api_getTranscationHistory } = CitizenApplyApiList()  // GETING API TO BE USED

  // OBJECT WHICH CONTAINS REQUIRED DATA TO CHILD COMPONENT
  const transactionRules = {
    api: {
      api_getTranscationHistory: { method: 'get', url: api_getTranscationHistory },
    },
    transactionInfo: {
      title: 'Citizen Payment History',
    },

    tableColumns: [
      {
        Header: "#",
        Cell: ({ row }) => <div className="pr-2">{row.index + 1}</div>,
      },
      {
        Header: "SAF No./Holding No.",
        Cell: ({ cell }) => <div className="pr-2">{nullToNA(cell.row.original?.saf_no)}</div>
      },
      {
        Header: "Transaction No.",
        Cell: ({ cell }) => <div className="pr-2">{nullToNA(cell.row.original?.tran_no)}</div>
      },
      {
        Header: "Payment Mode",
        Cell: ({ cell }) => <div className="pr-2">{nullToNA(cell.row.original?.payment_mode)}</div>

      },
      {
        Header: "Transaction Date",
        Cell: ({ cell }) => <div className="pr-2">{nullToNA(cell.row.original?.created_at)}</div>

      },
      {
        Header: "Amount",
        Cell: ({ cell }) => <div className="pr-2">{nullToNA(cell.row.original?.amount)}</div>

      },
      {
        Header: "Transaction By",
        Cell: ({ cell }) => <div className="pr-2">{nullToNA(cell.row.original?.transactionBy)}</div>

      },
      {
        Header: "Receipt",
        Cell: ({ cell }) => <button onClick={() => {
          if (cell.row.original.tran_type == 'Saf') {
            navigate(`/paymentReceipt/${cell.row.original?.tran_no}/saf`)
          } else {
            navigate(`/paymentReceipt/${cell.row.original?.tran_no}/holding`)
          }
        }} type="button" className="cypress_owner_add_update px-2 py-2.5 border border-indigo-500 text-indigo-500 font-medium text-xs leading-tight uppercase rounded shadow-xl hover:bg-indigo-700 hover:text-white hover:shadow-lg  active:shadow-lg transition duration-150 ease-in-out cursor-pointer">View Receipt</button>
      }
    ]
  }
  return (
    <>
      <TitleBar titleText="Property Transaction History" />
      <CustomErrorBoundary errorMsg="Bug in TransactionHistoryFactory" >
        <TransactionHistoryFactory transactionRules={transactionRules} />
      </CustomErrorBoundary>
    </>
  )
}

export default CitizenAllTransactions