import { useLocation, useNavigate, useParams } from "react-router-dom"
import { BiLeftArrowAlt } from 'react-icons/bi'
import { BsColumnsGap } from 'react-icons/bs'

function TitleBar(props) {
    const routeLocation = useLocation()


    // if (routeLocation.pathname == '/' || routeLocation?.pathname == '/login' || routeLocation ) {
    //     return
    // }

    const location = ['/', '/about', '/login', '/landingDashboard', '/propertyDashboard', '/view-water', '/track-water', '/view-trade', '/track-trade', '/editProfile', '/change-password/change', '/change-password/forget'].includes(routeLocation?.pathname)

    if(location){
        return
    }

    console.log('title status', props?.titleStatus)

    if(!props?.titleStatus){
        return
    }

    return (
        <>
            <div className="relative flex py-2 justify-start items-center mt-4">
                <div className="flex-1 flex pl-2 text-gray-700">
                    <span className="cursor-pointer hover:text-indigo-500" onClick={() => window.history.back()}><BiLeftArrowAlt className="inline" />Back</span>
                </div>
                <div className="flex justify-between items-center text-gray-800 text-xl font-semibold">
                    <BsColumnsGap className="inline mr-2" />{props?.titleText}
                </div>
            </div>
            <hr className="mb-4" />
           
        </>
    )
}

export default TitleBar